const applyFilters = (
  medias: ISocialMedia[],
): ISocialMedia[] => {
  return medias.filter((media) => {
    let matches = true;

    /*
    if (filters.status && cryptoOrder.status !== filters.status) {
      matches = false;
    }*/

    return matches;
  });
};

const applyPagination = (
  cryptoOrders: ISocialMedia[],
  page: number,
  limit: number
): ISocialMedia[] => {
  return cryptoOrders.slice(page * limit, page * limit + limit);
};

