import { Card } from '@mui/material';
import { useEffect, useState } from 'react';
import { api } from 'src/api/api';
import { ISocialMedia } from 'src/models/settings';
import BrandsTable from './ContactsTable';



function Contacts() {
  
  const [media, setMedia] = useState<ISocialMedia[]>([]);

  async function getAllSocialMedias(){
    const {data} = await api.get("/marcas");
    setMedia(data);
  }

  useEffect(()=>{
    getAllSocialMedias();
  }, [])

  return (
    <Card>
      <BrandsTable socialMedia={media?media:[]} panel={false} className=""/>
    </Card>
  );
}

export default Contacts;
